import { Component, OnInit } from '@angular/core';
import { first } from 'rxjs/operators';

import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';
import { HashMap } from 'src/app/shared/utilities/types.utilities';
import { MapLegendItem, ExploreMapSettings } from 'src/app/shared/utilities/settings.interfaces';
import { ANIMATION_SHOW_CONTRACT_EXPAND } from 'src/app/shared/globals/angular-animations';

@Component({
    selector: 'a-map-legend',
    templateUrl: './map-legend.component.html',
    styleUrls: ['./map-legend.component.scss'],
    animations: [ANIMATION_SHOW_CONTRACT_EXPAND]
})
export class ExploreMapLegendComponent extends BaseDirective implements OnInit {
    /** Mapping of legend item groups */
    public legend_items: HashMap<MapLegendItem[]> = {};
    /** Mapping of map colours */
    public colours: HashMap<string> = {};
    /** Whether group names should be displayed */
    public show_group_names: boolean;
    /** Whether the legend items should be shown */
    public show_legend = false;

    /** List of group names for legend items */
    public get group_names(): string[] {
        return Object.keys(this.legend_items);
    }

    constructor(private _service: ApplicationService) {
        super();
    }

    public ngOnInit(): void {
        this._service.initialised.pipe(first(_ => _)).subscribe(() => {
            const explore_settings: ExploreMapSettings =
                this._service.setting('app.explore') || ({} as any);
            this.legend_items = explore_settings.legend || {};
            this.colours = explore_settings.colors || {};
            this.show_group_names = !!explore_settings.show_legend_group_names;
        });
    }
}
