import {
    Component,
    OnInit,
    Input,
    OnChanges,
    SimpleChanges,
    ViewChild,
    ElementRef
} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { catchError, debounceTime, distinctUntilChanged, switchMap, map, first } from 'rxjs/operators';
import { Observable, Subject, of } from 'rxjs';

import { BaseDirective } from 'src/app/shared/base.directive';
import { User } from 'src/app/services/data/users/user.class';
import { Space } from 'src/app/services/data/spaces/space.class';
import { UsersService } from 'src/app/services/data/users/users.service';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';

import * as dayjs from 'dayjs';

// TODO: Add handling for role users. e.g. Fire Wardens, First Aiders
// TODO: Add handling for searching for features
// TODO: Add handling for searching for desks

@Component({
    selector: 'explore-search',
    templateUrl: './search.component.html',
    styleUrls: ['./search.component.scss']
})
export class ExploreSearchComponent extends BaseDirective implements OnInit, OnChanges {
    /** Type of data to show when searching */
    @Input() public type: 'space' | 'user' | 'feature' | 'desk' | 'any';
    /** Whether external changes are being processed */
    @Input() public autofocus: boolean;
    /** Display string for the placeholder of the search input */
    @Input() public placeholder: string;
    /** Whether search data is loading */
    public loading: boolean;
    /** Search filter value */
    public search_str: string;
    /** List of users from an API search */
    public search_results$: Observable<(User | Space)[]>;
    /** Post search and filtering list of items */
    public results: (User | Space)[] = [];
    /** Subject holding the value of the search */
    public search$ = new Subject<string>();

    /** Input field reference */
    @ViewChild('input', { static: false }) private input_field: ElementRef<HTMLInputElement>;

    constructor(
        private _users: UsersService,
        private _spaces: SpacesService,
        private _route: ActivatedRoute,
        private _router: Router
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first(_ => _)).subscribe(() => {
            this.subscription(
                'route.params',
                this._route.paramMap.subscribe(params => {
                    if (params.has('search_type')) {
                        this.type = params.get('search_type') as any;
                    }
                })
            );
            // Listen for input changes
            this.search_results$ = this.search$.pipe(
                debounceTime(300),
                distinctUntilChanged(),
                switchMap(query => {
                    this.loading = true;
                    return query.length >= 3 && (this.type === 'user' || this.type === 'any')
                        ? (this._users.query({
                              q: query.slice(0, 3),
                              cache: 60 * 1000
                          }) as Promise<User[]>)
                        : Promise.resolve([]);
                }),
                catchError(err => of([])),
                map((users: User[]) => {
                    const spaces = this._spaces.filter(_ => true);
                    const search = (this.search_str || '').toLowerCase();
                    const filter = item =>
                        (item.name || '').toLowerCase().indexOf(search) >= 0 ||
                        (item.email || '').toLowerCase().indexOf(search) >= 0;
                    let result_list = [];
                    if (this.type === 'space') {
                        result_list = spaces;
                    } else if (this.type === 'user') {
                        result_list = users;
                    } else {
                        result_list = (spaces as any).concat(users);
                    }
                    this.loading = false;
                    return result_list.filter(filter);
                })
            );
            // Process API results
            this.subscription(
                'search_results',
                this.search_results$.subscribe(list => {
                    this.results = list;
                    list.sort((a, b) => (a.name || '').localeCompare(b.name));
                    console.log('List:', list);
                })
            );
            this.search$.next(`${dayjs().valueOf()}`);
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.autofocus && this.autofocus) {
            this.focusField();
        }
    }

    public focusField() {
        /* istanbul ignore else */
        if (this.input_field && this.input_field.nativeElement) {
            this.input_field.nativeElement.focus();
        }
    }

    /**
     * Update the selected item to display the location for on the map
     * @param item Space or User to display location for
     */
    public selectOption(item: User | Space) {
        if (item instanceof Space) {
            this._router.navigate([], {
                relativeTo: this._route,
                queryParams: { space: item.id },
                queryParamsHandling: 'merge'
            });
        } else {
            this._router.navigate([], {
                relativeTo: this._route,
                queryParams: { user: item.email },
                queryParamsHandling: 'merge'
            });
        }
        this.search_str = item.name;
    }
}
