import { Component, EventEmitter, Inject, Output } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';

import { Booking } from 'src/app/services/data/bookings/booking.class';
import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';
import { HashMap, DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { Space } from 'src/app/services/data/spaces/space.class';
import { humaniseDuration } from 'src/app/shared/utilities/general.utilities';
import { User } from 'src/app/services/data/users/user.class';
import { CateringOrder } from 'src/app/services/data/catering/catering-order.class';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { UsersService } from 'src/app/services/data/users/users.service';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';

import * as dayjs from 'dayjs';
import { RECURRENCE_PERIODS } from 'src/app/shared/utilities/formatting.utilities';

@Component({
    selector: 'a-booking-confirm-modal',
    templateUrl: './booking-confirm.component.html',
    styleUrls: ['./booking-confirm.component.scss'],
})
export class BookingConfirmComponent extends BaseDirective {
    /** Emitter for user actions on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Display string for changes are being processed */
    public loading: string;
    /** Whether space availability is being checked */
    public checking_available: boolean;
    /** Whether to show all attendees */
    public show_attendees: boolean;
    /** Whether to show all attendees */
    public show_locations: boolean;
    /** Whether to show all catering orders */
    public show_orders: boolean;
    /** Whether booking has succeeded */
    public success: boolean;

    /** Whether booking needs to be accepted */
    public get is_request(): boolean {
        const options = {
            date: this.booking.changes.date || this.booking.date,
            duration: this.booking.changes.duration || this.booking.duration,
            host: this.organiser,
        };
        console.log('Spaces:', this.spaces);
        return this.spaces.reduce((request, space) => request || space.byRequest(options), false);
    }

    /** Booking to confirm changes to */
    public get booking(): Booking {
        return this._data.booking;
    }

    /** Title of the displayed event */
    public get title(): string {
        return this.booking.changes.title || this.booking.title;
    }

    /** Display string of when the event will occur */
    public get when(): string {
        const date_value = this.booking.changes.date || this.booking.date;
        const duration_value = this.booking.changes.duration || this.booking.duration;
        const all_day = this.booking.changes.all_day || this.booking.all_day;
        const date = dayjs(date_value);
        const end = date.add(duration_value, 'm');
        if (all_day || duration_value > 23 * 60) {
            return `${date.format('DD MMM YYYY')} - All Day`;
        } else {
            if (date.isSame(end, 'd')) {
                return `${date.format('DD MMM YYYY, h:mm A')} - ${end.format('h:mm A')}`;
            } else {
                return `${date.format('DD MMM YYYY, h:mm A')} - ${end.format(
                    'DD MMM YYYY, h:mm A'
                )}`;
            }
        }
    }

    /** Display string for the event's duration */
    public get duration(): string {
        const duration_value = this.booking.changes.duration || this.booking.duration;
        return humaniseDuration(duration_value);
    }

    /** Reccurence Range */
    public get recurrence_range(): string {
        if (!this.booking || !this.booking.recurrence || !this.booking.recurrence.period) {
            return '';
        }
        const start = dayjs(this.booking.recurrence.start).format('MMMM DD, YYYY');
        const end = dayjs(this.booking.recurrence.end).format('MMMM DD, YYYY');
        return `${start} until ${end}`;
    }

    public get recurrence_period(): string {
        if (!this.booking || !this.booking.recurrence) {
            return '';
        }
        return this.booking.recurrence.period;
    }

    /** List of spaces associated with the event */
    public get spaces(): readonly Space[] {
        return (this.booking.changes.space_list || this.booking.space_list).map(
            (space) => new Space(space)
        );
    }

    /** List of attendees associated with the event not including the host */
    public get attendees(): User[] {
        const attendees = this.booking.changes.attendees || this.booking.attendees;
        return attendees.filter((i) => i.email !== this.organiser.email);
    }

    /** Host of the event */
    public get organiser(): User {
        return this.booking.changes.organiser || this.booking.organiser || this._users.current;
    }

    /** Details about the event */
    public get notes(): string {
        return this.booking.changes.body || this.booking.body;
    }

    /** List of catering orders in the booking */
    public get catering(): CateringOrder[] {
        return (this.booking.changes.catering || this.booking.catering).map((order) => {
            const space: any = this.spaces.find((a_space) => a_space.id === order.location_id) || {
                level: {},
            };
            const building: any =
                this._org.buildings.find((bld) => bld.id === space.level.id) || {};
            return new CateringOrder({ ...order, location: space.name, symbol: building.currency });
        });
    }

    /** Map of changes to the booking */
    public get changes(): HashMap<boolean> {
        /* istanbul ignore else */
        if (!this.booking.id) {
            return {};
        }
        const changes = this.booking.changes;
        return Object.keys(changes).reduce((map, key) => {
            /* istanbul ignore else */
            if (changes[key]) {
                map[key] = changes[key] !== this.booking[key];
            }
            return map;
        }, {});
    }

    constructor(
        private _dialog_ref: MatDialogRef<BookingConfirmComponent>,
        @Inject(MAT_DIALOG_DATA) private _data: { booking: Booking },
        private _service: ApplicationService,
        private _org: OrganisationService,
        private _users: UsersService,
        private _spaces: SpacesService,
        private _router: Router
    ) {
        super();
        /* istanbul ignore else */
        if (!this._data.booking) {
            this._service.notifyError('No booking data to confirm');
            this._dialog_ref.close();
        }
    }

    /**
     * Save booking changes
     */
    public saveChanges() {
        this.loading = 'Checking availability of selected spaces';
        this._dialog_ref.disableClose = true;
        const onError = (message) => (err) => {
            this.loading = null;
            this._dialog_ref.disableClose = false;
            this._service.notifyError(`${message}. Error: ${err}`);
        };
        this.checkSpaceAvailability().then(() => {
            this.loading = 'Processing booking request...';
            this.booking.save().then(() => {
                this.loading = null;
                this.success = true;
                this._dialog_ref.disableClose = false;
                this.event.emit({ reason: 'done' });
                this._service.notifySuccess(
                    `Successfully ${this.booking.id ? 'updated' : 'created'} booking`
                );
            }, onError(`Error ${this.booking.id ? 'updating' : 'creating'} booking`));
        }, onError('Error checking space availability'));
    }

    public newBooking() {
        localStorage.removeItem('ACA_STAFF.booking_form_data');
        this._router.navigate(['/book', 'spaces']);
        this._dialog_ref.close();
    }

    /**
     * Check whether the selected spaces are available for the selected time period
     */
    public checkSpaceAvailability(): Promise<void> {
        const spaces = this.spaces;
        /* istanbul ignore else */
        if (
            (this.booking.id && !this.changes.date && !this.changes.duration) ||
            (spaces && spaces.length <= 0)
        ) {
            return Promise.resolve();
        }
        return new Promise((resolve, reject) => {
            this._spaces
                .available({
                    room_ids: spaces.map((space) => space.id).join(','),
                    date: this.booking.changes.date || this.booking.date,
                    duration: this.booking.changes.duration || this.booking.duration,
                })
                .then(
                    (space_list) => {
                        for (const space of space_list) {
                            /* istanbul ignore else */
                            if (!space.was_available) {
                                return reject(
                                    `${space.name} is not available at the select time period.`
                                );
                            }
                        }
                        resolve();
                    },
                    (err) => reject(err)
                );
        });
    }
}
