import {
    Component,
    OnInit,
    Input,
    Output,
    EventEmitter,
    ElementRef,
    Renderer2,
    ViewChild,
} from '@angular/core';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Identity, Point } from 'src/app/shared/utilities/types.utilities';

import * as dayjs from 'dayjs';
import { eventToPoint } from 'src/app/shared/utilities/general.utilities';

const BLOCK_SIZE = 30;
const MINUTES_IN_DAY = 24 * 60;

@Component({
    selector: 'a-timeline',
    templateUrl: './timeline.component.html',
    styleUrls: ['./timeline.component.scss'],
})
export class TimelineComponent extends BaseDirective implements OnInit {
    /** Currently selected datetime */
    @Input() public date: number;
    /** Emitter for changes to the selected datetime */
    @Output() public dateChange = new EventEmitter<number>();
    /** List of time blocks to select */
    public blocks: Identity[] = [];
    /** Starting point for time move events */
    private _start_position: Point;
    /** Bounding box of the timeline container */
    private _box: ClientRect;

    /** Whether selected date time is the current day */
    public get is_today() {
        return dayjs().isSame(dayjs(this.date), 'd');
    }

    /** Offset of the current time from the start of the day */
    public get offset() {
        const date = dayjs();
        const diff = date.diff(date.startOf('d'), 'm');
        return (diff / MINUTES_IN_DAY) * 100;
    }

    /** Details of the currently selected datetime */
    public get active_time(): Identity {
        const date = dayjs(this.date);
        const diff = date.diff(date.startOf('d'), 'm');
        return {
            id: date.isSame(dayjs(), 'd') ? 'Today' : date.format('DD MMM YYYY'),
            name: date.format('h:mm A'),
            left: (diff / MINUTES_IN_DAY) * 100,
        };
    }

    @ViewChild('container', { static: true }) private _container: ElementRef<HTMLDivElement>;

    constructor(private _renderer: Renderer2) {
        super();
    }

    public ngOnInit(): void {
        this.blocks = this.generateBlocks();
        this.checkDate();
        this.interval('check_date', () => this.checkDate(), 15 * 1000);
    }

    /**
     * Generate the display blocks for the timeline
     */
    public generateBlocks(): Identity[] {
        const start = dayjs().startOf('d');
        const end = start.endOf('d');
        const blocks: Identity[] = [];
        let current_time = start;
        while (current_time.isBefore(end)) {
            const is_hour = current_time.minute() === 0;
            blocks.push({
                id: current_time.format('HH:mm'),
                name: is_hour ? current_time.format('h') : '',
                is_hour,
                left: (current_time.diff(start, 'm') / MINUTES_IN_DAY) * 100,
            });
            current_time = current_time.add(BLOCK_SIZE, 'm');
        }
        return blocks;
    }

    /**
     * Change the date of the active date time
     * @param amount Number of days to change by
     */
    public changeDate(amount: number = 1) {
        let date = dayjs(this.date);
        date = date.add(amount, 'd');
        const now = dayjs();
        this.date = date.isBefore(now, 'm') ? now.valueOf() : date.valueOf();
        this.dateChange.emit(this.date);
    }

    public startMove(event: MouseEvent | TouchEvent) {
        const position = eventToPoint(event);
        this._start_position = position;
        this._box = this._container.nativeElement.getBoundingClientRect();
        const type = event instanceof MouseEvent ? 'mouse' : 'touch';
        this.subscription(
            'move',
            this._renderer.listen('window', `${type}move`, (e) => this.onMove(e))
        );
        this.subscription(
            'end',
            this._renderer.listen('window', `${type === 'mouse' ? 'mouseup' : 'touchend'}`, (e) => {
                this.unsub('move');
                this.unsub('end');
            })
        );
    }

    public onMove(event: MouseEvent | TouchEvent) {
        const position = eventToPoint(event);
        const y_change = (this._start_position.x - position.x) / this._box.width;
        const time_change = -Math.round(y_change * MINUTES_IN_DAY);
        /* istanbul ignore else */
        if (time_change !== 0) {
            let old_date = dayjs(this.date);
            let date = old_date;
            date = date.add(time_change, 'm');
            date = date.minute(Math.floor(Math.round(date.minute() / 5)) * 5);
            this.date = date.valueOf();
            this.dateChange.emit(this.date);
            /* istanbul ignore else */
            if (Math.abs(old_date.diff(date, 'm')) > 0) {
                this._start_position = position;
            }
            this.checkDate();
        }
    }

    /** Check that active datetime is not in the past */
    private checkDate() {
        const date = dayjs(this.date);
        let now = dayjs();
        now = now.minute(Math.floor(Math.ceil(now.minute() / 5)) * 5);
        /* istanbul ignore else */
        if (date.isBefore(now, 'm')) {
            this.date = now.valueOf();
            this.dateChange.emit(this.date);
        }
    }
}
