import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MapPinComponent } from '@acaprojects/ngx-interactive-map';
import { first } from 'rxjs/operators';

import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from 'src/app/shared/base.directive';
import { MapStatus, MapListener } from '../status-managers/status.interfaces';
import { HashMap, Point } from 'src/app/shared/utilities/types.utilities';
import { MapFeature } from '@acaprojects/ngx-interactive-map/lib/helpers/map.interfaces';
import { MapRadiusComponent } from '@acaprojects/ngx-interactive-map';
import { ExploreMapSettings } from 'src/app/shared/utilities/settings.interfaces';
import { MatDialog } from '@angular/material/dialog';
import {
    BookingRulesModalComponent,
    BookingRulesModalData,
} from 'src/app/overlays/booking-rules-modal/booking-rules-modal.component';
import { Building } from 'src/app/services/data/organisation/building.class';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { UsersService } from 'src/app/services/data/users/users.service';

@Component({
    selector: 'explore-map-view',
    templateUrl: './map-view.component.html',
    styleUrls: ['./map-view.component.scss'],
})
export class MapViewComponent extends BaseDirective implements OnInit {
    /** Zoom level of the map */
    public zoom: number;
    /** Center offset of the map */
    public center: Point = { x: 0.5, y: 0.5 };
    /** Mapping of elements to CSS styles */
    public style_map: HashMap<HashMap<string | number>> = {};
    /** List of listeners for the map */
    public listeners: MapListener[] = [];
    /** List of features to render on the map */
    public features: MapFeature[] = [];
    /** ID of the active level to display on the map */
    public active_level: string;
    /** Mapping of status lists to types */
    public status_map: { spaces?: MapStatus[]; desks?: MapStatus[]; zones?: MapStatus[] } = {};
    /** Mapping of listener lists to types */
    public listener_map: {
        spaces?: MapListener[];
        desks?: MapListener[];
        zones?: MapListener[];
    } = {};
    /** Mapping of features lists to types */
    public feature_map: {
        spaces?: MapFeature[];
        desks?: MapFeature[];
        zones?: MapFeature[];
    } = {};
    /** Message to display over the map */
    public message: string;
    /** Whether location data is loading */
    public loading: boolean;
    /** Point or element on the map to focus */
    public focus: string | Point;
    /** Feature on the map to focus */
    public focus_feature: MapFeature;
    /** Current shown time */
    public date: number;
    /** Application settings for the explore component */
    public settings: ExploreMapSettings;

    /** URL of the active map */
    public get map_url(): string {
        const level = this._org.levelWithID(this.active_level) || { map_url: '' };
        return level.map_url || '';
    }

    /** Whether current level has booking rules associated with it */
    public get has_booking_rules(): boolean {
        const level = this._org.levelWithID(this.active_level) || {
            building_id: '',
        };
        return !!(this._org.buildings.find((bld) => bld.id === level.building_id) || {})
            .booking_details;
    }

    constructor(
        private _service: ApplicationService,
        private _route: ActivatedRoute,
        private _dialog: MatDialog,
        private _org: OrganisationService,
        private _users: UsersService,
        private _spaces: SpacesService
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            /* istanbul ignore else */
            if (!this.active_level) {
                const bld = this._org.building;
                if (bld && bld.levels.length) {
                    this.active_level = bld.levels[0].id;
                }
            }
            this.settings = this._service.setting('app.explore') || {};
            this.subscription(
                'route.query',
                this._route.queryParamMap.subscribe((params) => {
                    /* istanbul ignore else */
                    if (params.has('space')) {
                        this.loadSpaceLocation(params.get('space'));
                    }
                    /* istanbul ignore else */
                    if (params.has('user')) {
                        this.loadUserLocation(params.get('user')).then(
                            (user) => {
                                this.loading = false;
                                this.message = this.focus
                                    ? ''
                                    : `Unable to locate ${user ? user.name : params.get('user')}`;
                            },
                            (err) => {
                                this.loading = false;
                                this.message = `Unable to locate user`;
                                /* istanbul ignore else */
                                if (err && err.status !== 404) {
                                    this._service.notifyError(
                                        `Error loading user location. ${err.message}`
                                    );
                                }
                            }
                        );
                    }
                })
            );
        });
    }

    public openBookingRulesModal() {
        const level = this._org.levelWithID(this.active_level) || {
            building_id: '',
        };
        const building: Building =
            this._org.buildings.find((bld) => bld.id === level.building_id) || ({} as any);
        this._dialog.open<BookingRulesModalComponent, BookingRulesModalData>(
            BookingRulesModalComponent,
            {
                data: {
                    building,
                },
            }
        );
    }

    /**
     * Process changes to statuses
     */
    public processStatuses() {
        const styles: HashMap<HashMap<string | number>> = {};
        (this.status_map.spaces || []).forEach((space) => (styles[`#${space.id}`] = space.styles));
        (this.status_map.desks || []).forEach((desk) => (styles[`#${desk.id}`] = desk.styles));
        (this.status_map.zones || []).forEach((zone) => (styles[`#${zone.id}`] = zone.styles));
        this.style_map = styles;
    }

    /**
     * Process changes to statuses
     */
    public processListeners() {
        let listeners: MapListener[] = [];
        listeners = listeners.concat(this.listener_map.spaces || []);
        listeners = listeners.concat(this.listener_map.desks || []);
        listeners = listeners.concat(this.listener_map.zones || []);
        this.listeners = listeners;
    }

    /**
     * Process changes to statuses
     */
    public processFeatures() {
        let features: MapFeature[] = [];
        /* istanbul ignore else */
        if (this.focus_feature) {
            features = features.concat([this.focus_feature]);
        }
        features = features.concat(this.feature_map.spaces || []);
        features = features.concat(this.feature_map.desks || []);
        features = features.concat(this.feature_map.zones || []);
        this.features = features;
    }

    /**
     * Load and store location details for the space
     * @param space_id Engine system ID of the space to locate
     */
    private loadSpaceLocation(space_id: string) {
        const space = this._spaces.find(space_id);
        /* istanbul ignore else */
        if (space) {
            this.message = `Loading location of ${space.name}`;
            this.loading = true;
            this.active_level = space.level.id;
            this.focus = `area-${space.map_id}-status`;
            this.focus_feature = {
                id: `area-${space.map_id}-status`,
                coordinates: null,
                content: MapPinComponent,
                data: {
                    text: space.name,
                },
            };
            this.zoom = 1;
            this.loading = false;
            this.message = '';
            console.log('Focus:', this.focus_feature)
            this.processFeatures();
        }
    }

    /**
     * Load and store the location for the user
     * @param user_email Email address of the selected user
     */
    private async loadUserLocation(user_email: string) {
        this.focus = null;
        this.focus_feature = null;
        this.loading = true;
        this.message = `Loading user details...`;
        const user = await this._users.show(user_email);
        /* istanbul ignore else */
        if (!user) return user;
        this.message = `Loading location for ${user.name}...`;
        const location = await user.locate();
        /* istanbul ignore else */
        if (!location) return user;
        this.active_level = location.level.id;
        if (location.fixed) {
            this.focus = location.id;
            this.focus_feature = {
                id: location.id,
                coordinates: null,
                content: MapPinComponent,
                data: {
                    text: `${user.name} is here`,
                },
            };
            this.zoom = 1;
        } else {
            this.focus = {
                x: location.x,
                y: location.y,
            };
            this.focus_feature = {
                coordinates: {
                    x: location.x,
                    y: location.y,
                },
                content: MapRadiusComponent,
                data: {
                    text: `${user.name} is here`,
                    diameter: location.confidence || 5,
                },
            };
        }
        this.zoom = 1;
        this.loading = false;
        this.message = '';
        this.processFeatures();
        return user;
    }
}
