import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd, NavigationStart } from '@angular/router';
import { ApplicationService } from 'src/app/services/app.service';
import { BaseDirective } from 'src/app/shared/base.directive';

import { HashMap } from 'src/app/shared/utilities/types.utilities';

export interface BookingFlowEvent {
    /** Type of flow event */
    type: 'previous' | 'next' | 'reset' | 'close' | 'error';
    /** Step which the event was emitted from */
    step: string;
    /** Additional metadata for the event */
    metadata?: HashMap;
}

@Component({
    selector: 'app-bookings',
    templateUrl: './bookings.component.html',
    styleUrls: ['./bookings.component.scss'],
})
export class BookingsComponent extends BaseDirective implements OnInit {
    /** Booking flow */
    public flow: 'spaces' | 'desks';
    /** Whether to show the overlay menu */
    public show_menu: boolean;

    constructor(
        private _service: ApplicationService,
        private _route: ActivatedRoute,
        private _router: Router
    ) {
        super();
    }

    public ngOnInit(): void {
        this._service.title = 'Book';
        this.subscription(
            'route.params',
            this._route.paramMap.subscribe((params) => {
                /* istanbul ignore else */
                if (params.has('flow')) {
                    this.flow = params.get('flow') as any;
                }
            })
        );
        this.subscription(
            'router.events',
            this._router.events.subscribe((event) => {
                /* istanbul ignore else */
                if (event instanceof NavigationStart) {
                    /* istanbul ignore else */
                    if (event.url.indexOf('book') < 0) {
                        localStorage.removeItem('ACA_STAFF.booking_form_data');
                    }
                }
            })
        );
    }
}
