import { Component, OnInit, Inject } from '@angular/core';
import {
    MAP_OVERLAY_DATA,
    MAP_LOCATION
} from '@acaprojects/ngx-interactive-map';

import { BaseDirective } from 'src/app/shared/base.directive';
import { Space } from 'src/app/services/data/spaces/space.class';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { statusFromBookings } from 'src/app/services/data/bookings/booking.utilities';
import { ApplicationService } from 'src/app/services/app.service';
import { Point } from 'src/app/shared/utilities/types.utilities';
import { UsersService } from 'src/app/services/data/users/users.service';

export interface SpaceInfoData {
    /** Current time to show details for */
    date?: number;
    /** Space to show details for */
    space: Space;
    /** Active bookings for the space */
    bookings: Booking[];
}

@Component({
    selector: 'a-space-info',
    templateUrl: './space-info.component.html',
    styleUrls: ['./space-info.component.scss']
})
export class ExploreSpaceInfoComponent extends BaseDirective implements OnInit {
    /** Status of the space */
    public status: string;
    /** String of time until next booking */
    public available_until: string;

    /** Named position of the element location on the map */
    public get quadrant(): 'top left' | 'top right' | 'bottom left' | 'bottom right' {
        /* istanbul ignore if */
        if (!this._position) {
            return 'top left';
        }
        if (this._position.y < .5) {
            if (this._position.x < .5) {
                return 'top left';
            } else {
                return 'top right';
            }
        } else {
            if (this._position.x < .5) {
                return 'bottom left';
            } else {
                return 'bottom right';
            }
        }
    }

    /** CSS class for the current status of the space */
    public get status_class(): string {
        return (this.status || '')
            .toLowerCase()
            .split(' ')
            .join('-');
    }

    /** Space to display on the component */
    public get space(): Space {
        /* istanbul ignore if */
        if (!this._data) {
            return null;
        }
        return this._data.space;
    }

    /** List of bookings from the space for the currently displayed time */
    public get bookings(): Booking[] {
        /* istanbul ignore if */
        if (!this._data) {
            return [];
        }
        return this._data.bookings || [];
    }

    constructor(
        @Inject(MAP_OVERLAY_DATA) private _data: SpaceInfoData,
        @Inject(MAP_LOCATION) private _position: Point,
        private _users: UsersService
    ) {
        super();
    }

    public ngOnInit(): void {
        this.processBookings();
        this.interval('update_bookings', () => this.processBookings(), 60 * 1000);
    }

    /**
     * Calculate availability of space based off given bookings
     */
    public processBookings(): void {
        /* istanbul ignore if */
        if (!this.space) {
            return;
        }
        const status = statusFromBookings(
            this.bookings,
            this.space.bookable,
            this.space.byRequest({
                date: this._data.date,
                duration: 30,
                host: this._users.current
            }),
            this._data.date
        );
        this.status = status.status;
        this.available_until = status.available_until;
    }
}
