
import { MockHttpRequestHandler } from '@placeos/ts-client';

import { MOCK_ORG } from './organisation.mock';
import { DOMAIN } from './common.mock';

window.control = window.control || {};
window.control.systems = window.control.systems || {};
window.control.handlers = window.control.handlers || [];

export const MOCK_BUILDINGS = [{
    id: 'zone_bld-01',
    zone_id: 'zone_bld-01',
    name: 'Sydney',
    settings: {
        discovery_info: {
            timezone: 'Australia/Sydney',
            neighbourhoods: {
                'zone_lvl-01': {
                    'Test Searchable': 'Device_Bench'
                }
            },
            visitor_space: `sydney-01.02@${DOMAIN}`,
            levels: [
                {
                    level_id: 'zone_lvl-33',
                    level_name: 'Level 1',
                    number: 1,
                    map_url: 'assets/maps/sydney/level_33.svg',
                    floor_type: 'staff',
                },
                {
                    level_id: 'zone_lvl-35',
                    level_name: 'Level 2',
                    number: 2,
                    map_url: 'assets/maps/sydney/level_35.svg',
                    floor_type: 'client',
                },
                {
                    level_id: 'zone_lvl-36',
                    level_name: 'Level 2',
                    number: 2,
                    map_url: 'assets/maps/sydney/level_36.svg',
                    floor_type: 'client',
                }
            ],
        },
    },
}, {
    id: 'zone_bld-02',
    zone_id: 'zone_bld-02',
    name: 'London Post',
    settings: {
        discovery_info: {
            timezone: 'Europe/London',
            visitor_space: `london-01.02@${DOMAIN}`,
            levels: [
                {
                    level_id: 'zone_lvl-10',
                    level_name: 'Level 10',
                    number: 1,
                    map_url: 'assets/maps/london_jermyn/level_10.svg',
                    floor_type: 'staff',
                },
                {
                    level_id: 'zone_lvl-11',
                    level_name: 'Level 11',
                    number: 11,
                    map_url: 'assets/maps/london_jermyn/level_11.svg',
                    floor_type: 'client',
                }
            ],
        },
    },
}];

MOCK_BUILDINGS.push({ ...MOCK_BUILDINGS[0], name: 'London' });

MOCK_ORG[0].discovery_info.buildings = MOCK_BUILDINGS;

setTimeout(() => {
    window.control.handlers.push({
        path: '/api/engine/v2/zones',
        metadata: MOCK_BUILDINGS,
        callback: (event) => {
            if (event.query_params.tags === 'building') {
                return { total: MOCK_BUILDINGS.length, results: MOCK_BUILDINGS };
            } else if (event.query_params.tags === 'org') {
                return { total: MOCK_ORG.length, results: MOCK_ORG };
            } else if (event.query_params.tags === 'level') {
                return { total: 0, results: [] };
            }
        }
    } as MockHttpRequestHandler);
});
