import {
    Component,
    Input,
    Output,
    EventEmitter,
    OnInit,
    OnDestroy,
    SimpleChanges,
    OnChanges,
} from '@angular/core';
import { first } from 'rxjs/operators';

import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';
import { Space } from 'src/app/services/data/spaces/space.class';
import { HashMap } from 'src/app/shared/utilities/types.utilities';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { ExploreSpaceInfoComponent } from '../overlays/space-info/space-info.component';
import { MapStatus, MapListener, SpaceStatus } from './status.interfaces';

import * as dayjs from 'dayjs';
import { MatDialog } from '@angular/material/dialog';
import {
    ExploreBookingModalComponent,
    ExploreBookingModalData,
} from '../overlays/booking-modal/booking-modal.component';
import { MapFeature } from '@acaprojects/ngx-interactive-map/lib/helpers/map.interfaces';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { UsersService } from 'src/app/services/data/users/users.service';

@Component({
    selector: '[space-status]',
    template: `
        <div class="bindings" *ngIf="space_list && is_today">
            <i
                *ngFor="let space of space_list"
                binding
                [sys]="space.id"
                mod="Bookings"
                bind="today"
                (modelChange)="processSpaceBookings(space, $event)"
            ></i>
        </div>
        <ng-content></ng-content>
    `,
})
export class ExploreSpaceStatusComponent extends BaseDirective
    implements OnInit, OnChanges, OnDestroy {
    /** ID of the zone to grab the spaces */
    @Input() public zone: string;
    /** Updates the status based of the given time. Unix epoch in milliseconds */
    @Input() public date: number;
    /** Emitter for changes to the space status */
    @Output() public status = new EventEmitter<MapStatus[]>();
    /** Emitter for changes to the space status */
    @Output() public listeners = new EventEmitter<MapListener[]>();
    /** Emitter for changes to the space status */
    @Output() public features = new EventEmitter<MapFeature[]>();
    /** List of spaces to grab the statuses for */
    public space_list: Space[] = [];
    /** Mapping of spaces to active bookings */
    public active_bookings: HashMap<Booking[]> = {};
    /** Mapping of colours to statuses */
    public colour_map: HashMap<string> = {};

    /** Whether selected date time is the current day */
    public get is_today() {
        return dayjs().isSame(dayjs(this.date), 'd');
    }

    constructor(
        private _service: ApplicationService,
        private _users: UsersService,
        private _spaces: SpacesService,
        private _dialog: MatDialog
    ) {
        super();
    }

    public ngOnInit(): void {
        this._service.initialised.pipe(first((_) => _)).subscribe(() => {
            this.colour_map = this._service.setting('app.explore.colors') || {};
            this.subscription(
                'spaces',
                this._spaces.listen('list').subscribe(() => this.loadSpaces())
            );
            this.interval(
                'update_status',
                () => {
                    this.updateSpaceBookings();
                    this.calculateStatus();
                },
                60 * 1000
            );
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.zone || changes.date) {
            this.loadSpaces();
        }
        if (changes.zone) {
            this.clearSpaces();
        }
    }

    public ngOnDestroy() {
        this.clearSpaces();
    }

    /**
     * Determine the availability statuses for all the spaces in the set zone
     */
    public calculateStatus() {
        this.timeout('process_statuses', () => {
            const statuses: MapStatus[] = [];
            for (const space of this.space_list) {
                const status = this.getStatus(space, this.date);
                const id =
                    space.map_id.indexOf('area') < 0 ? `area-${space.map_id}-status` : space.map_id;
                statuses.push({
                    id,
                    styles: {
                        fill: this.colour_map[`space-${status}`] || '#000',
                        opacity: 0.6,
                    },
                    status_id: status,
                });
            }
            this.status.emit(statuses);
        });
    }

    /**
     * Get the status of the space at the set time
     * @param space Space to get the status for
     */
    private getStatus(space: Space, datetime?: number): SpaceStatus {
        if (!space.bookable) {
            return 'not-bookable';
        }
        const bookings = this.active_bookings[space.id] || [];
        const date = dayjs(datetime).startOf('m').second(1);
        const current_booking = bookings.find((booking) => {
            const start = dayjs(booking.date).startOf('m');
            const end = start.add(booking.duration, 'm');
            return date.isAfter(start, 's') && date.isBefore(end, 'm');
        });
        if (current_booking) {
            return 'unavailable';
        }
        const options = {
            date: dayjs(this.date).valueOf(),
            duration: 30,
            host: this._users.current,
        };
        const rules = space.rulesFor(options);
        if (rules.hide) {
            return 'not-bookable';
        } else if (!rules.auto_approve) {
            return 'requestable';
        }
        return 'available';
    }

    /**
     * Process raw booking data for the given space
     * @param space Space associated with the bookings
     * @param data List of booking data
     */
    public processSpaceBookings(space: Space, data: HashMap[]) {
        this.timeout(
            `space-${space.id}`,
            () => {
                const bookings = (data || []).map(
                    (bkn_data) => new Booking({ ...bkn_data, room_ids: [space.id] })
                );
                this.active_bookings[space.id] = bookings;
                this.calculateStatus();
            },
            10
        );
    }

    /**
     * Update the booking data for space on future dates
     */
    public updateSpaceBookings() {
        /* istanbul ignore else */
        if (this.is_today) {
            return;
        }
        this.timeout(
            'update_spaces',
            () => {
                const date = dayjs(this.date).startOf('d');
                this._spaces
                    .query({
                        from: date.unix(),
                        to: date.endOf('d').unix(),
                        show_declined: false,
                        zone_ids: this.zone,
                        cache: 10 * 1000,
                    })
                    .then((spaces) => {
                        spaces.forEach((space) =>
                            this.processSpaceBookings(space, [...space.bookings])
                        );
                    });
            },
            50
        );
    }

    /**
     * Load listeners for space data
     */
    private loadSpaces() {
        this.space_list = this._spaces.filter((space) => space.zones.indexOf(this.zone) >= 0);
        const listeners: MapListener[] = [];
        for (const space of this.space_list) {
            const id =
                space.map_id.indexOf('area') < 0 ? `area-${space.map_id}-status` : space.map_id;
            listeners.push({
                id,
                event: 'mouseenter',
                callback: () => this.showSpaceInfo(space),
            });
            listeners.push({
                id,
                event: 'mouseleave',
                callback: () => this.features.emit([]),
            });
            if (space.bookable) {
                listeners.push({
                    id,
                    event: 'click',
                    callback: () => this.openBookingModal(space),
                });
            }
        }
        this.listeners.emit(listeners);
        this.updateSpaceBookings();
        this.calculateStatus();
    }

    /**
     * Clear current space and booking data
     */
    private clearSpaces() {
        delete this.active_bookings;
        this.active_bookings = {};
    }

    /**
     * Update features to include space info for a space
     * @param space Space to show details for
     */
    private showSpaceInfo(space: Space) {
        const id = space.map_id.indexOf('area') < 0 ? `area-${space.map_id}-status` : space.map_id;
        this.features.emit([
            {
                id,
                coordinates: null,
                content: ExploreSpaceInfoComponent,
                data: {
                    space,
                    bookings: this.active_bookings[space.id],
                    date: this.date,
                },
            },
        ]);
    }

    /**
     * Open explore booking modal for the given space
     * @param space Space to make booking for
     */
    private openBookingModal(space: Space) {
        const status = this.getStatus(space, this.date);
        if (status !== 'unavailable' && status !== 'not-bookable') {
            this._dialog.open<ExploreBookingModalComponent, ExploreBookingModalData>(
                ExploreBookingModalComponent,
                {
                    width: 'auto',
                    height: 'auto',
                    data: {
                        date: this.date,
                        space,
                        bookings: this.active_bookings[space.id],
                    },
                }
            );
        } else {
            this._service.notifyInfo(
                `${space.name} is unavailable at the selected time. Please select another space or time and try again.`
            );
        }
    }
}
