import {
    Component,
    OnInit,
    Inject,
    Output,
    EventEmitter,
    ViewChild,
    ElementRef,
} from '@angular/core';
import { FormGroup, Validators, AbstractControl, FormControl } from '@angular/forms';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { Space } from 'src/app/services/data/spaces/space.class';
import { HashMap, DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { BookingNote } from 'src/app/services/data/bookings/booking.interfaces';
import { OrganisationService } from 'src/app/services/data/organisation/organisation.service';
import { UsersService } from 'src/app/services/data/users/users.service';

import * as dayjs from 'dayjs';

export interface RequirementDetailsModalData {
    spaces: Space[];
    notes_field: AbstractControl;
    codes_field: AbstractControl;
    head_counts_field: AbstractControl;
}

@Component({
    selector: 'a-requirement-details-modal',
    templateUrl: './requirement-details-modal.component.html',
    styleUrls: ['./requirement-details-modal.component.scss'],
})
export class RequirementDetailsModalComponent implements OnInit {
    /** Emitter for modal events */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Mapping of spaces to notes */
    public notes: HashMap<string> = {};
    /** Mapping of spaces to cost codes */
    public codes: HashMap<string> = {};
    /** Mapping of spaces to estimated head count */
    public head_counts: HashMap<string> = {};
    /** Mapping of spaces to form fields */
    public form: HashMap<FormGroup> = {};
    /** Map of spaces to whether they need a charge code */
    public needs_charge_code: HashMap<boolean> = {};
    /** Index of the active space */
    public active_space: number = 0;

    @ViewChild('content', { static: true }) private _content_el: ElementRef<HTMLElement>;

    /** List of selected spaces */
    public get space_list(): Space[] {
        return this._data.spaces;
    }

    public get active_form(): FormGroup {
        return this.form[this.space_list[this.active_space].id];
    }

    /** Whether form fields are valid */
    public get is_valid(): boolean {
        return this.space_list.reduce((valid, space) => valid && this.form[space.id].valid, true);
    }

    constructor(
        private _org: OrganisationService,
        private _users: UsersService,
        @Inject(MAT_DIALOG_DATA) private _data: RequirementDetailsModalData
    ) {}

    public ngOnInit(): void {
        const note_list = this._data.notes_field.value.filter(
            (note: BookingNote) => note.type === 'equipment'
        );
        this.notes = note_list.reduce((map, note) => {
            map[note.space] = note.message;
            return map;
        }, {});
        this.codes = { ...this._data.codes_field.value };
        this.head_counts = { ...this._data.head_counts_field.value };
        this.generateFormFields();
    }

    public submit(): void {
        this.space_list.forEach((space) => this.form[space.id].markAllAsTouched());
        /* istanbul ignore else */
        if (this.is_valid) {
            // Update notes
            const notes = this._data.notes_field.value.filter(
                (note: BookingNote) => note.type !== 'equipment'
            );
            const new_notes = this.space_list.map(
                (space) =>
                    ({
                        author: this._users.current.name,
                        type: 'equipment',
                        message: this.form[space.id].controls.notes.value,
                        space: space.id,
                        date: dayjs().valueOf(),
                    } as BookingNote)
            );
            this._data.notes_field.setValue(notes.concat(new_notes).filter((note) => note.message));
            this._data.codes_field.setValue(
                this.space_list.reduce((map, space) => {
                    map[space.id] = this.form[space.id].controls.code.value;
                    return map;
                }, {})
            );
            this._data.head_counts_field.setValue(
                this.space_list.reduce((map, space) => {
                    map[space.id] = this.form[space.id].controls.head_count.value;
                    return map;
                }, {})
            );
            this.event.emit({ reason: 'done' });
        } else {
            this._scrollToBottom();
        }
    }

    /** Generate form fields for each space */
    private generateFormFields(): void {
        for (const space of this.space_list) {
            const building = this._org.buildings.find((bld) => bld.id === space.level.building_id);
            this.needs_charge_code[space.id] = building.required.equipment_code;
            this.form[space.id] = new FormGroup({
                notes: new FormControl(this.notes[space.id] || ''),
                code: new FormControl(
                    this.codes[space.id] || '',
                    building.required.equipment_code ? [Validators.required] : []
                ),
                head_count: new FormControl(this.head_counts[space.id] || '', [
                    Validators.required,
                ]),
            });
        }
    }

    /** Scroll the content container to the bottom */
    private _scrollToBottom(): void {
        this._content_el.nativeElement.scrollTo({
            top: this._content_el.nativeElement.scrollHeight,
            left: 0,
            behavior: 'smooth',
        });
    }
}
