import { Component, Output, EventEmitter, Inject } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { MapPinComponent } from '@acaprojects/ngx-interactive-map';

import { BaseDirective } from '../../shared/base.directive';
import { BuildingLevel } from '../../services/data/organisation/level.class';
import { Space } from '../../services/data/spaces/space.class';
import { MapFeature } from '@acaprojects/ngx-interactive-map/lib/helpers/map.interfaces';
import { Point } from 'src/app/shared/utilities/types.utilities';

@Component({
    selector: 'view-room-modal',
    templateUrl: './view-room-modal.component.html',
    styleUrls: ['./view-room-modal.component.scss'],
})
export class ViewRoomModalComponent extends BaseDirective {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter();

    public features: MapFeature[] = [];
    /** Space to show the location of on the map */
    public get space(): Space {
        return this._data.space;
    }

    public get level(): BuildingLevel {
        return this.space.level;
    }

    constructor(@Inject(MAT_DIALOG_DATA) private _data: { space: Space }) {
        super();
        this.features = [this.focus_feature];
    }

    /** Point on map to focus on */
    public get focus_feature(): MapFeature {
        /* istanbul ignore else */
        if (!this.space) {
            return null;
        }
        return {
            id: `area-${this.space.map_id}-status`,
            coordinates: null,
            content: MapPinComponent,
            data: {
                back: '#fff',
                fore: '#d31145',
            },
        };
    }

    public get focus(): string | Point {
        return this.focus_feature ? this.focus_feature.id : null;
    }
}
