import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import { BaseDirective } from 'src/app/shared/base.directive';
import { ApplicationService } from 'src/app/services/app.service';
import { Booking } from 'src/app/services/data/bookings/booking.class';
import { generateBookingForm } from 'src/app/services/data/bookings/booking.utilities';
import {
    AvailableBookingFields,
    BookingFlowSettings,
} from 'src/app/shared/utilities/settings.interfaces';
import { HashMap, DialogEvent } from 'src/app/shared/utilities/types.utilities';
import { BookingFlowEvent } from '../bookings.component';
import { MatDialog } from '@angular/material/dialog';
import { BookingConfirmComponent } from '../overlays/booking-confirm/booking-confirm.component';
import { first } from 'rxjs/operators';
import {
    RequirementDetailsModalComponent,
    RequirementDetailsModalData,
} from '../overlays/requirement-details-modal/requirement-details-modal.component';
import {
    CateringDetailsModalComponent,
    CateringDetailsModalData,
} from '../overlays/catering-details-modal/catering-details-modal.component';
import { SpacesService } from 'src/app/services/data/spaces/spaces.service';
import { UsersService } from 'src/app/services/data/users/users.service';

@Component({
    selector: 'booking-space-flow',
    templateUrl: './space-flow.component.html',
    styleUrls: ['./space-flow.component.scss'],
})
export class BookingSpaceFlowComponent extends BaseDirective implements OnInit {
    /** Form fields */
    public form: FormGroup;
    /** Form data store */
    public booking: Booking;
    /** Step of the booking flow that is currently active */
    public flow_step: string;
    /** Application settings for bookings */
    public settings: BookingFlowSettings;

    /** List of available booking fields for the form */
    public get available_fields(): AvailableBookingFields[] {
        return this.settings.show_fields || [];
    }

    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _users: UsersService,
        private _route: ActivatedRoute,
        private _router: Router,
        private _dialog: MatDialog
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this.settings = this._service.setting('app.booking');
            this.subscription(
                'route.params',
                this._route.paramMap.subscribe((params) => {
                    /* istanbul ignore else */
                    if (params.has('step')) {
                        this.flow_step = params.get('step');
                    }
                })
            );
            this.loadFormData();
        });
    }

    /**
     * Handle events from child components
     * @param event Booking flow event
     */
    public handleFlowEvent(event: BookingFlowEvent) {
        switch (event.type) {
            case 'next':
                this.toNextFlowStep(event);
                break;
            case 'previous':
                this.toPreviousFlowStep(event);
                break;
            case 'reset':
                this.resetForm();
                break;
        }
    }

    private toNextFlowStep(event: BookingFlowEvent) {
        const space_list = this.form.controls.space_list.value;
        switch (event.step) {
            case 'search':
                if (
                    space_list &&
                    space_list.length &&
                    this.settings.multiple_spaces &&
                    (!event.metadata || !event.metadata.ref || event.metadata.ref !== 'equipment')
                ) {
                    this.openEquipmentDetailsModal();
                } else {
                    if (this.form.controls.has_catering && this.form.controls.has_catering.value) {
                        this.navigate('catering');
                    } else {
                        this.navigate('form');
                        this.confirmBooking();
                    }
                }
                break;
            case 'catering':
                const order_list = this.form.controls.catering.value;
                if (
                    order_list &&
                    order_list.length &&
                    (!event.metadata || !event.metadata.ref || event.metadata.ref !== 'details')
                ) {
                    this.openCateringDetailsModal();
                } else {
                    this.navigate('form');
                    this.confirmBooking();
                }
                break;
            default:
                if (
                    !this.form.controls.needs_space ||
                    (this.form.controls.needs_space.value && (!space_list || !space_list.length || !this.form.controls.id.value))
                ) {
                    this.navigate('search');
                } else {
                    this.confirmBooking();
                }
                break;
        }
    }

    private toPreviousFlowStep(event: BookingFlowEvent) {
        switch (event.step) {
            case 'search':
                this.navigate('form');
                break;
            case 'catering':
                this.navigate('search');
                break;
        }
    }

    private navigate(route: string) {
        this._router.navigate(['book', 'spaces', route], {
            queryParamsHandling: 'preserve',
        });
        this.flow_step = route;
    }

    /** Clear user changes to the form data */
    private resetForm() {
        this.booking.clearPendingChanges();
        delete this.form;
        this.form = generateBookingForm(
            this.booking,
            this._users.current,
            this.available_fields
        );
    }

    /**
     * Load existing form data or generate a new booking form
     */
    private loadFormData(): void {
        let form_data: HashMap = {};
        /* istanbul ignore else */
        if (localStorage) {
            const form_string = localStorage.getItem('ACA_STAFF.booking_form_data');
            /* istanbul ignore else */
            if (form_string) {
                try {
                    form_data = JSON.parse(form_string);
                } catch (e) {}
            }
        }
        this.booking = new Booking(form_data);
        this.subscription(
            'form_changes',
            this.booking.changeEvents.subscribe(() => this.saveFormData())
        );
        this.form = generateBookingForm(
            this.booking,
            this._users.current,
            this.available_fields
        );
        /* istanbul ignore else */
        /** Add local pending data */
        if (form_data && !form_data.id) {
            for (const key in form_data) {
                if (this.form.controls[key]) {
                    this.form.controls[key].setValue(form_data[key]);
                } else if (this.booking.hasOwnProperty('')) {
                    this.booking.storePendingChange(key as any, form_data[key]);
                }
            }
        }
        /* istanbul ignore else */
        if (form_data && form_data.room_ids && this.form.controls.space_list) {
            this.form.controls.space_list.setValue(
                form_data.room_ids.map((id) => this._spaces.find(id) || { id, name: id })
            );
        }
    }

    private saveFormData() {
        /* istanbul ignore else */
        if (localStorage && this.booking) {
            localStorage.setItem(
                'ACA_STAFF.booking_form_data',
                JSON.stringify(this.booking.toJSON(true))
            );
        }
    }

    /** Open modal for adding equipment requirements to booking */
    private openEquipmentDetailsModal() {
        const ref = this._dialog.open<
            RequirementDetailsModalComponent,
            RequirementDetailsModalData
        >(RequirementDetailsModalComponent, {
            maxWidth: '95vw',
            width: '32em',
            data: {
                spaces: this.form.controls.space_list.value,
                notes_field: this.form.controls.notes,
                codes_field: this.form.controls.equipment_codes,
                head_counts_field: this.form.controls.expected_attendees,
            },
        });
        this.subscription(
            'modal_events',
            ref.componentInstance.event.subscribe((event: DialogEvent) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    this.toNextFlowStep({
                        type: 'next',
                        step: 'search',
                        metadata: { ref: 'equipment' },
                    });
                    ref.close();
                }
            })
        );
        ref.afterClosed().subscribe(() => this.unsub('modal_events'));
    }

    /** Open modal for adding equipment requirements to booking */
    private openCateringDetailsModal() {
        const ref = this._dialog.open<CateringDetailsModalComponent, CateringDetailsModalData>(
            CateringDetailsModalComponent,
            {
                maxWidth: '95vw',
                width: '32em',
                data: {
                    spaces: this.form.controls.space_list.value,
                    notes_field: this.form.controls.notes,
                    catering: this.form.controls.catering,
                },
            }
        );
        this.subscription(
            'modal_events',
            ref.componentInstance.event.subscribe((event: DialogEvent) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    this.toNextFlowStep({
                        type: 'next',
                        step: 'catering',
                        metadata: { ref: 'details' },
                    });
                    ref.close();
                }
            })
        );
        ref.afterClosed().subscribe(() => this.unsub('modal_events'));
    }

    /**
     * Open modal to confirm the booking details
     */
    private confirmBooking() {
        /* istanbul ignore else */
        if (!this.booking.has_changes) {
            this._service.notifyInfo('No changes have been made to booking');
            return;
        }
        /* istanbul ignore else */
        if (this.form.valid) {
            const dialog_ref = this._dialog.open(BookingConfirmComponent, {
                width: '32em',
                maxWidth: '95vw',
                maxHeight: '95vh',
                data: { booking: this.booking },
            });
            dialog_ref.componentInstance.event.subscribe((event: DialogEvent) => {
                /* istanbul ignore else */
                if (event.reason === 'done') {
                    this._router.navigate(['/schedule']);
                }
            });
        }
    }
}
